	module.exports = {"data":{"markdownRemark":{"html":"<hr>\n<h1>Recommendations</h1>\n<p><strong>Beginner Takeaways</strong>: 3/10</p>\n<p><strong>Intermediate Takeaways</strong>: 10/10</p>\n<p><strong>Expert Takeaways</strong>: 7/10</p>\n<p><strong>Readability</strong>: 10/10</p>\n<h1>Takeaways</h1>\n<p>All vimmers should know three names: Tim Pope, Learn Vim The Hard Way Guy, and Drew Neil.  </p>\n<p>Tim Pope writes the best vim plugins hands down.  </p>\n<p>Drew Neil runs the vimcasts website.  </p>\n<p>Practical Vim is a treasure trove of vim tips.<br>\nAny vimmer will take a ton of stuff out of this book.<br>\nIf you have used vimcasts before, understand that this book is not the same.  </p>\n<p>...It is 1,000x better.  </p>\n<p>I really like vimcasts, but Practical Vim knocks it out of the park.<br>\nI read this book in 4 days while on vacation.  I could not put it down.  Then when I got back I skimmed it again.<br>\nThat is how awesome this book is.  </p>\n<hr>\n<p><em>If you decide to take my recommendation, and purchase the book. Please use the link below, so I can get something back</em>  </p>\n<iframe src=\"http://rcm-na.amazon-adsystem.com/e/cm?lt1=_blank&bc1=FFFFFF&IS2=1&npa=1&bg1=FFFFFF&fc1=000000&lc1=0000FF&t=richsonicom-20&o=1&p=8&l=as4&m=amazon&f=ifr&ref=ss_til&asins=1934356980\" style=\"width:120px;height:240px;\" scrolling=\"no\" marginwidth=\"0\" marginheight=\"0\" frameborder=\"0\"></iframe>","frontmatter":{"title":"Book Review: Practical Vim"},"fields":{"date":"2013-12-02"}}},"pathContext":{"url":"/posts/2013-12-02-practical-vim/"}}

